.vuiInfoTable {
  width: 100%;
  table-layout: fixed;
  border: 1px solid var(--vui-color-border-light);

  thead {
    background-color: var(--vui-color-light-shade);
    border-bottom: 1px solid var(--vui-color-border-light);
  }

  tbody tr {
    border-bottom: 1px solid var(--vui-color-border-light);
  }

  th {
    font-size: $fontSizeSmall;
    font-weight: $fontWeightBold;
    padding: $sizeXs $sizeS;
    text-align: left;
  }

  td {
    font-size: $fontSizeStandard;
  }
}

.vuiInfoTable--alignTop {
  td {
    vertical-align: top;
  }
}

.vuiInfoTable--alignMiddle {
  td {
    vertical-align: middle;
  }
}

.vuiInfoTable--alignBottom {
  td {
    vertical-align: bottom;
  }
}

.vuiInfoTable--paddingXxs {
  td {
    padding: $sizeXxs $sizeS;
  }
}

.vuiInfoTable--paddingXs {
  td {
    padding: $sizeXs $sizeS;
  }
}

.vuiInfoTable--paddingS {
  td {
    padding: $sizeS $sizeS;
  }
}

.vuiInfoTableRow--sectionHeader {
  background-color: var(--vui-color-light-shade);
  border-bottom: none !important;
}

.vuiInfoTableRow--footer {
  background-color: var(--vui-color-light-shade);
}
